import wechat from '@/api/wechat'
import {mapGetters, mapActions} from 'vuex'

export default {
  data: ()=> ({
    attention: 0
  }),
  methods: {
    ...mapActions([
      'set_attention',
      'set_share',
    ]),
    // 
    getSign(){
      let data = {url: location.href.split('#')[0]}
      return wechat.sign(data)
    },
    // 
    isWeiXin() {
      let weixinDevice = true
      let ua = window.navigator.userAgent.toLowerCase();
      if(ua.match(/MicroMessenger/i)=='micromessenger'){
        weixinDevice = true
      }else {
        weixinDevice = false
      }
      return weixinDevice
    },
    // 
    auth(){
      //let obj = {type: type}
      let query = this.$route.query
      if(!!sessionStorage.getItem('auth') && sessionStorage.getItem('auth')=='true'){
        console.log('authcallback')
        this.callback({code: query.code, state: query.state})
        return
      }else {
        if(!!query.code){
          console.log('codecallback')
          this.callback({code: query.code, state: query.state})
        }else {
          wechat.auth(obj).then(res=> {
            if(res.data.code==0){
              let url = res.data.data
              sessionStorage.setItem('auth', 'true')
              window.location.href = url
            }
          })
        }
      }
    },
    callback(obj={}){
      return wechat.callback(obj).then(response=> {
        if(response.data.code==0){
          // 
          console.log(response.data)
          this.attentionFn()
          console.log('success')
        }else {
          // 
          console.log('')
        }
      })
    },
    // 
    wxshare(title, desc, link, imgUrl, callback) {
      let self = this
      wx.ready(function() {
        wx.onMenuShareTimeline({
          title: title,   // 
          desc: desc,     // 
          link: link,     // 
          imgUrl: imgUrl, // 
          success: function() {
            callback();
            console.log(link)
            self.set_share(false)
          },
          cancel: function() {
          	self.set_share(false)
          }
        });
        wx.onMenuShareAppMessage({
          title: title,
          desc: desc,
          link: link,
          imgUrl: imgUrl,
          success: function() {
            callback();
            console.log(link)
            self.set_share(false)
          },
          cancel: function() {self.set_share(false)}
        });
      });
    },
    // 
    wxShare(title='title', desc='desc', link='http://5iqianshou.com/hongbao/#/middle', imgUrl='http://image-cdn.fengchuanshidai.com/activezq/share_icon.jpg') {
      this.getSign().then(responce=> {
        if (responce.data.code == 0) {
          let data = responce.data.data
          wx.config({
            debug: false, // ,apialertpclogpc
            appId: 'wx1dfc72997585a1e5', // 
            timestamp: data.timestamp, // 
            nonceStr: data.nonceStr, // 
            signature: data.signature, // 1
            jsApiList: ['onMenuShareTimeline', 'onMenuShareAppMessage'] // JSJS2
          });
          this.wxshare( title, desc, link, 'http://image-cdn.fengchuanshidai.com/activezq/share_icon1.jpg', function() {
              console.log("");
            }
          );
        } else {
          alert(responce.data.userMessage);
        }
      })
    },
    attentionFn(){
      wechat.careWeb().then(care=> {
        if(care.data.code==0){
          if(care.data.data==1){
            this.attention = 1
            sessionStorage.setItem('attention', 1)
            this.set_attention(1)
          }else {
            this.attention = 0
            sessionStorage.setItem('attention', 0)
            this.set_attention(0)
          }
        }
      })
    }
  }
}



